/*16px*/
body {
  background: white;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: rgb(15, 23, 42);
}

p {
  max-width: 40em;
  margin-bottom: 1.5rem;
}

h1,
h2,
h3 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: "Spectral", serif;
  font-weight: 700;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

small {
  font-size: 0.8rem;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}

/* buttons */
